<template>
  <v-content class="ma-0 pa-0 py-4">
    <v-card class="mx-auto my-2" max-width="95%">
      <v-card-title>
        Total all banks by currency
      </v-card-title>
      <v-card-text>
        <app-table class="my-2" :loading="loading" :headers="headers" :items="total" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly">
        </app-table>
      </v-card-text>
    </v-card>
    

    <v-card   v-for="bank in banks" :key="bank._id" class="my-2 mx-auto" max-width="95%" >
      <v-card-title> 
        <span v-html="$options.filters.country(bank.country, true, false, 0.7)" class="mx-3"></span> {{bank.number}} 
        <v-spacer></v-spacer>
        <v-tooltip bottom>
          <template v-slot:activator="{ attrs, on }" t>
            <v-btn icon v-bind="attrs" v-on="on" @click="showTrans(bank)"><v-icon>mdi-bank-transfer</v-icon></v-btn>
          </template>
          <span>Show Bank Transactions</span>
        </v-tooltip>
      </v-card-title>
      <v-card-text>

        <app-table 
        class="my-2" 
        :loading="loading" 
        :headers="headers" :items="banksTransactions[bank._id]" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly">
        </app-table>
       
      </v-card-text>
    </v-card>


  </v-content>
</template>
<script>
 
import AppTable from './../DataTable'
import collections from './../../collections'
export default {
  name: 'WakelsTotalDashboard',
  components: {
    AppTable
  },
  props: {
    dates: Array
  },
  data () {
    return {
      loading: false,
      total: [],
      banks: [],
      banksTransactions: {}
    }
  },
  computed: {
    headers () {
       return  collections.get('banktransaction').items.total.headers 
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotal',
      immediate: true
    }
  },
  methods: {
   
    showTrans (bank) {
      const path ={ path: 'banktransaction', query: { bank: bank._id, created_on: this.dates.join('|') }}
      this.$router.push(path)
    },
    async fetchTotal () { 
      this.total = [],
      this.banks = [],
      this.banksTransactions = {}
      // get totals
      this.loading = true
      const total = await this.get({ collection: 'banktransaction/total', query: {
        created_on: this.dates.join('|')
      } })
      this.total = total.data
    

      const banks = await this.get({ collection: 'bank' })
      this.banks = banks.data.docs
      
       await Promise.all(this.banks.map(async bank => {
        const trans =  await this.get({ collection: 'banktransaction/total', query: {
          created_on: this.dates.join('|'),
          bank: bank._id
        } }) 
        this.banksTransactions[bank._id] = trans.data
       
      }))
      

      this.loading = false
    }
  }
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>